<script>
	import {
		required,
		email
	} from "vuelidate/lib/validators";
	import axios from 'axios'
	/**
	 * written by 张文彧
	 */
	export default {
		data() {
			/**
			 * 验证规则
			 */
			var checkUserName = (rule, value, callback) => {
				if (!value) {
					return callback(new Error('用户名不能为空'));
				}
				// setTimeout(() => {
				//   if (!Number.isInteger(value)) {
				//     callback(new Error('请输入数字值'));
				//   } else {
				//     if (value < 18) {
				//       callback(new Error('必须年满18岁'));
				//     } else {
				//       callback();
				//     }
				//   }
				// }, 1000);
			};
			var validatePass = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('请输入密码'));
				} else {
					if (this.ruleForm.checkPass !== '') {
						this.$refs.ruleForm.validateField('checkPass');
					}
					callback();
				}
			};
			var validatePass2 = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('请再次输入密码'));
				} else if (value !== this.ruleForm.pass) {
					callback(new Error('两次输入密码不一致!'));
				} else {
					callback();
				}
			};
			return {
				isAllFill:false,
				user: {
					username: "",
					email: "",
					password: ""
				},
				submitted: false,
				regError: null,
				tryingToRegister: false,
				isRegisterError: false,
				registerSuccess: false,
				ruleForm: {
					pass: '',
					checkPass: '',
					username: ''
				},
				rules: {
					pass: [{
						validator: validatePass,
						trigger: 'blur'
					}],
					checkPass: [{
						validator: validatePass2,
						trigger: 'blur'
					}],
					username: [{
						validator: checkUserName,
						trigger: 'blur'
					}]
				}
			};
		},
		computed: {
			notification() {
				return this.$store ? this.$store.state.notification : null;
			}
		},
		validations: {
			user: {
				username: {
					required
				},
				email: {
					required,
					email
				},
				password: {
					required
				}
			}
		},
		created() {
			document.body.classList.add("auth-body-bg");
		},
		watch: {
			ruleForm: {
			    handler() {
					console.log(77)
				  	this.isAllFill = this.ruleForm.pass!='' && this.ruleForm.checkPass!='' && this.ruleForm.username!=''
			    },
			    deep: true
			}
		},
		methods: {
			/**
			 * @param {Object} response
			 * 根据接口返回的状态码提供提示
			 */
			giveMessage: function(response) {
				if(response.data.code == 200){
					this.$message({
							type: 'success',
							message: '注册成功!'
						}),
						this.$router.push({
							path: '/'
						})
				} else{
					alert(response)
				}	
			},
			submitForm: function() {
				console.log(this.ruleForm.checkPass, this.ruleForm.username)
				if(this.isAllFill){
					/**
					 * ajax请求注册接口
					 */
					axios
						.post('http://localhost:8848/shiro/register', null, {
							params: {
								username: this.ruleForm.username,
								password: this.ruleForm.checkPass
							}
						})
						.then(response => (
							// this.userList = response.data.list
							this.giveMessage(response)
					
					
						))
						.catch(function(error) { // 请求失败处理
							console.log(error);
						});
				} else{
					this.$message({
							type: 'warning',
							message: '请将信息填写完整!'
					})
				}
				
			}
		}
	};
</script>

<template>
	<div>
		<div class="home-btn d-none d-sm-block">
			<a href="/">
				<i class="mdi mdi-home-variant h2 text-white"></i>
			</a>
		</div>
		<div>
			<div class="container-fluid p-0">
				<div class="row no-gutters">
					<div class="col-lg-4">
						<div class="authentication-page-content p-4 d-flex align-items-center min-vh-100">
							<div class="w-100">
								<div class="row justify-content-center">
									<div class="col-lg-9">
										<div>
											<div class="text-center">
												<div>
													<a href="/" class="logo">
														<img src="@/assets/images/logo-dark.png" height="20"
															alt="logo" />
													</a>
												</div>

												<h4 class="font-size-18 mt-4">注册账户</h4>
												<p class="text-muted">获得你的免费账号.</p>
											</div>

											<div class="p-2 mt-5">
												<b-alert v-model="registerSuccess" class="mt-3" variant="success"
													dismissible>Registration successfull.</b-alert>

												<b-alert v-model="isRegisterError" class="mt-3" variant="danger"
													dismissible>{{regError}}</b-alert>

												<b-alert variant="danger" class="mt-3" v-if="notification.message" show
													dismissible>{{notification.message}}</b-alert>

												<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm"
													class="demo-ruleForm" style="text-align: center;">
													<el-form-item label="用户名" prop="age">				
														<el-input v-model="ruleForm.username"></el-input>
													</el-form-item>
													<el-form-item label="密码" prop="pass">
														<el-input type="password" v-model="ruleForm.pass"
															autocomplete="off"></el-input>
													</el-form-item>
													<el-form-item label="确认密码" prop="checkPass">
														<el-input type="password" v-model="ruleForm.checkPass"
															autocomplete="off"></el-input>
													</el-form-item>								
													<el-form-item style="margin-top: 10px;">
														<el-button class="btn btn-primary w-md waves-effect waves-light" type="primary" @click="submitForm('ruleForm')" >注册
														</el-button>
													</el-form-item>
												</el-form>

											</div>

											<div class="mt-5 text-center">
												<p>

													<router-link tag="a" to="/login"
														class="font-weight-medium text-primary">点此登录</router-link>
												</p>
												<p>
													© 2021 scdx-cloud
													<i class="mdi mdi-heart text-danger"></i> by scdx
												</p>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
					<div class="col-lg-8">
						<div >
							<div class="bg-overlay"></div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
